import React from 'react';
import { Container } from 'react-bootstrap';
import Menu from '../components/menu/Menu';
import Seperator from '../components/seperator/Seperator';
import About from './about/About';
import './Home.css';
//import { Container, Row, Col } from 'react-bootstrap';
import View from './view/View';

const viewRef: any = React.createRef();
const profileRef: any = React.createRef();
const githubRef = React.createRef();
const contactRef = React.createRef();

const createMenuMap = () => {
    const menu = new Map();
    menu.set("Home",viewRef);
    menu.set("About",profileRef);
    menu.set("Github",githubRef);
    menu.set("Contact",contactRef);
    return menu;
}

const menuMap = createMenuMap();

const onClickMenuItem = (id:string) => {
    console.log(id);
    menuMap.get(id).current.scrollIntoView();
}

const Home = () => {
  return (
    <Container fluid>
        <Container ref={viewRef}>     
            <View/>
        </Container>
        <Container fluid>
            <Menu onClickMenuItem={onClickMenuItem} menuItems={menuMap}/>
        </Container>
        <Seperator color={"#2E8B57"}/>
        <Container ref={profileRef} fluid>           
            <About/>
        </Container>
        <Seperator color={"#008080"}/>
    </Container>
  );
}

export default Home;