import './View.css';
import {useState, useEffect} from 'react';
import { Container } from 'react-bootstrap';
import cloud from "../../resources/images/cloud.png"
import tree from "../../resources/images/tree.png"
import Namecard from '../../components/namecard/Namecard';

const scrollPercent = (scrollHeight:number) => {

    const screenHeight = window.innerHeight*0.5;
    if(scrollHeight >= screenHeight-40){
        return 1;
    }
    else{
        return scrollHeight/(screenHeight-40);
    }

}

const shift = (factor:number, constant:number ,scrollHeight:number) => {
    const screenWidth = window.innerWidth;
    let screenShift = 20;
    if(screenWidth<=1000){
        screenShift = 50;
        factor = factor/2;
        //constant = constant/2;
    }
    if(screenWidth<=500){
        screenShift = 100;
        factor = factor/3;
        //constant = constant/3;
    }
    return (scrollPercent(scrollHeight)*factor)+constant+screenShift;
}

const View = () => {

    const [scroll, setScroll] = useState(0);

    useEffect(() => {
        window.addEventListener("scroll", () => {
            setScroll(window.scrollY);
        });
    },[]);

    return (
        <Container fluid className="view-name-container">
            <Namecard/>
            <div className="background"></div>
            <div className="clouds">
                <img id="cloud1" className="cloud" style={{top:(-1*shift(0,200,scroll)),left:(-1*shift(0,300,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud2" className="cloud" style={{top:(-1*shift(100,100,scroll)),left:(-1*shift(100,200,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud3" className="cloud" style={{top:(-1*shift(200,0,scroll)),left:(-1*shift(200,100,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud4" className="cloud" style={{top:(-1*shift(0,200,scroll)),right:(-1*shift(0,300,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud5" className="cloud" style={{top:(-1*shift(100,100,scroll)),right:(-1*shift(100,200,scroll))}} src={cloud} alt="cloud"/>
                <img id="cloud6" className="cloud" style={{top:(-1*shift(200,0,scroll)),right:(-1*shift(200,100,scroll))}} src={cloud} alt="cloud"/>
                <img id="tree1" className="tree" style={{bottom:(-1*shift(0,350,scroll)),left:(shift(350,-300,scroll))}} src={tree} alt="tree"/>
                <img id="tree2" className="tree" style={{bottom:(-1*shift(0,350,scroll)),left:(shift(200,-150,scroll))}} src={tree} alt="tree"/>
                <img id="tree3" className="tree" style={{bottom:(-1*shift(0,350,scroll)),left:(shift(50,0,scroll))}} src={tree} alt="tree"/>
                <img id="tree4" className="tree" style={{bottom:(-1*shift(0,350,scroll)),right:(shift(350,-300,scroll))}} src={tree} alt="tree"/>
                <img id="tree5" className="tree" style={{bottom:(-1*shift(0,350,scroll)),right:(shift(200,-150,scroll))}} src={tree} alt="tree"/>
                <img id="tree6" className="tree" style={{bottom:(-1*shift(0,350,scroll)),right:(shift(50,0,scroll))}} src={tree} alt="tree"/>
            </div>
        </Container>
    );
}

export default View;