import './Namecard.css';
import {Row, Col } from 'react-bootstrap';

const Namecard = ()=> {
  return (
    <Row className="namecard">
        <Col>
            <label className="description-box">
                Hi, I'm <label className="name">Atul Aditya</label>.<br/>
                I'm a full-stack web developer.
            </label>
        </Col>
    </Row>
  );
}

export default Namecard;