import './Seperator.css';
import { Container } from 'react-bootstrap';

interface IProps {
    color : string
}

const Seperator = (props:IProps) => {

    return (
        <Container fluid style={{backgroundColor:props.color}} className="seperator-container">
        </Container>
    );
}

Seperator.defaultProps = {
    color : '#00a1a7'
}

export default Seperator;