import React, { RefObject } from 'react';
import './Menu.css';
import {Navbar, Nav } from 'react-bootstrap';

interface IProps {
    menuItems: Map<string,RefObject<any>>
    onClickMenuItem: any
}

const Menu = (props:IProps) => {
  return (
        <Navbar expand="sm" className="menu" bg="menubar" variant="dark" fixed="bottom">
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                {Array.from(props.menuItems.keys()).map(item => (
                    <Nav.Link href={"#"+item} className="menu-btn"
                    onClick={()=>props.onClickMenuItem(item)}>
                            {item}
                    </Nav.Link>
                ))}
                </Nav>
            </Navbar.Collapse>
        </Navbar>
  );
}

Menu.defaultProps = {
    menuItems : new Map()
}

export default Menu;